import React, {useState} from 'react'
import './Login.css'

function Login(props) {
    const [userName,setUserName] = useState("");
    const [password,setPassword] = useState("");

    const handleUserNameChange = function(event){
        setUserName(event.target.value);
    };
    const handlePasswordChange = function(event){
        setPassword(event.target.value);
    };

    const handleLogin = function(){
        if(userName!="" && password!="")
            props.handleLogin({userName,password});
        else
            alert('All fields are compulsory!');
    };

    return (
        <div className="login-page">
            <div className="row">
                <div className="col-sm-4 col-lg-4 col-md-4"></div>
                <div className="title">EasyMaintain Login</div>
            </div>
            <div className="row">
                <div className="col-sm-4 col-lg-4 col-md-4"></div>
                <label className="col-sm-1 col-lg-1 col-md-1 labelClass">UserName</label>
                <div className="col-sm-4 col-lg-4 col-md-4">
                    <input type="text" placeholder="Enter your username" onChange={handleUserNameChange}></input>
                </div>
            </div>
            <div className="row">
                <div className="col-sm-4 col-lg-4 col-md-4"></div>
                <label className="col-sm-1 col-lg-1 col-md-1 labelClass">Password</label>
                <div className="col-sm-4 col-lg-4 col-md-4">
                    <input type="password" placeholder="Enter your password" onChange={handlePasswordChange}></input>
                </div>
            </div>
            <div className="row">
                <div className="col-sm-6 col-lg-6 col-md-6">
                    <button className="btn btn-primary float-right login-btn" onClick={handleLogin}>Login</button>
                </div>
            </div>
        </div>
    )
}

export default Login
