import React from 'react'
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import CreateTask from '../Components/CreateTask'
import TasksView from '../Components/TasksView'

function RouterPaths(props) {    
    const handleLogout = props.handleLogout;
    return (
        <div>
            <Router>
                <Switch>
                    <Route path='/' exact component={(props) => (<TasksView handleLogout={handleLogout} {...props} />)}/>
                    <Route path='/tasks' exact component={(props) => (<TasksView handleLogout={handleLogout} {...props}/>)}/>
                    <Route path='/create-task' exact component={(props) => (<CreateTask {...props}/>)}/>
                    <Route path='/create-task/:taskId' exact component={(props) => (<CreateTask {...props}/>)}/>
                </Switch>
            </Router>
        </div>
    )
}

export default RouterPaths
