import React,{useState,useEffect,useContext} from 'react';
import './TasksView.css'
import "@progress/kendo-theme-material/dist/all.css";
import { Grid, GridColumn } from "@progress/kendo-react-grid";
import UserContext from "./UserContext";
import BootstrapTable from 'react-bootstrap-table-next';

function TasksView(props) {
    const [tasks, setTasks] = useState([]);
    const {authToken,user}=useContext(UserContext);

    useEffect(() => {
        fetchTasks();
    }, []);

    const fetchTasks = async function(){
        const response = await fetch('/tasks',{
            method: 'GET', 
            headers: {
            'Content-Type': 'application/json',
             authToken: authToken
            },
        });
        const taskData = await response.json();
        if(taskData.error){
            alert(taskData.error);
        }
        else{
            setTasks(taskData);
        }
    }

    const handleLogout = function(){
        props.handleLogout();
    };

    const handleCreate = function(){
        props.history.push('/create-task');
    }

    const handleEdit = (row) => {
        const taskId = row._id;
        props.history.push(`/create-task/${taskId}`)
    }

    const getEditButton = (cell, row) => {
        return <button className="btn btn-primary" onClick={() => handleEdit(row)}>Edit</button>
    }

    const products = tasks;
    const columns = [{
    dataField: 'taskName',
    text: 'Record Name'
    }, {
    dataField: 'taskCategory',
    text: 'Category'
    }, {
    dataField: 'taskDescription',
    text: 'Description'
    },
    {
        dataField: 'taskValues',
        text: 'Actions',
        formatter : getEditButton
    }
    ];

    return (
        <div className="tasks-page">
            <div className="row records">
                <div className="col-md-3 col-sm-3 col-lg-3">
                    <label className="records-label">Records</label>
                </div>
                <div className="col-md-6 col-sm-6 col-lg-6"></div>
                <div className="col-md-3 col-sm-3 col-lg-3">
                    <button className="btn btn-primary record-button" onClick={handleCreate} >Create a Record</button>
                    <button className="btn btn-primary" onClick={handleLogout}>Logout</button>
                </div>
            </div>
            <BootstrapTable keyField='_id' data={ products } columns={ columns } />
        </div>
    )
}

export default TasksView
