import React, {useState} from 'react'
import './App.css';
import LoginSignup from './Pages/LoginSignup'
import RouterPaths from './Pages/RouterPaths'
import UserContext from "./Components/UserContext";

function App() {

  const [userData,setUserData] = useState({
    authToken:localStorage.getItem("authToken"),
    user:JSON.parse(localStorage.getItem("user")),
  });

  const handleLogin = async function(loginCredentials){
    const response = await fetch('/users/login', {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json',
            }, 
            body: JSON.stringify(loginCredentials)
        });
      const userData = await response.json()
      if(userData.error){
        alert(userData.error)
      }else{
        initializeLocalStorage(userData);
      }
  }

  const handleLogout = async function(){
    const response = await fetch('/users/logout', {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json',
            authToken : localStorage.getItem("authToken")
            }, 
        });
    const responseJson = await response.json();
    if(responseJson.error){
      alert(responseJson.error);
    }else{
      emptyLocalStorage();
    }        
  }

  const emptyLocalStorage = function(){
    localStorage.clear();
    setUserData({});
  }

  const initializeLocalStorage = function(userData){
    localStorage.setItem("authToken",userData.token);
    localStorage.setItem("user",JSON.stringify(userData.user));
    setUserData({
      authToken:userData.token,
      user:userData.user
    });
  }

  const pageToShow = 
    (userData.authToken && userData.user) ?
    (<UserContext.Provider value={userData}>
       <RouterPaths handleLogout={handleLogout} />
    </UserContext.Provider>)
    :
       <LoginSignup handleLogin={handleLogin} />

  return (
    <div className="App">
      {pageToShow}
    </div>
  );
}

export default App;
